import React, {Component} from "react";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";

export default class MulticartCancelOrderPopup extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <OraModal id={this.props.id} open={this.props.openPopup} narrow={true} showClose={false}>
                <div className="l-page-row u-font-bold" >
                    <div className="l-row u-spacing-top-l" hidden={!(this.props.reservation&&this.props.reservation.paymentStatus) || !this.props.showWarning}>
                        <div className="l-col l-col-12">
                            {this.props.cancelOrderWarning}
                            <OraButton className="g-green3-c" type="text" size="small" modal={false} onClick={this.props.onCorrectiveInvoiceButtonClick}>
                                {this.props.cancelOrderWarningLink}
                            </OraButton>.
                        </div>
                    </div>
                    <div className="l-row u-spacing-top-l">
                        <div className="l-col l-col-12">
                            {this.props.cancelOrderQuestion} {this.props.autoClose}
                        </div>
                    </div>
                    <div className="l-row u-spacing-top-l">
                        <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 large-offset-by-4">
                            <OraButton className="u-w-100" type="secondary" modal={this.props.autoClose} onClick={this.handleClosePopup.bind(this)}>
                                {this.props.noButtonText}
                            </OraButton>
                        </div>
                        <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 ">
                            <OraButton className="u-w-100 g-green3-bg" type="primary" modal={this.props.autoClose} onClick={this.handleOnConfirm.bind(this)}>
                                {this.props.yesButtonText}
                            </OraButton>
                        </div>
                    </div>
                </div>
            </OraModal>
        )
    }

    handleClosePopup() {
        this.props.onClose(false);
    }

    handleOnConfirm() {
        this.props.onClose(false);
        this.props.onConfirm();
    }

    openInNewWindow(data) {
        window.open(data, '_blank', 'toolbar=0,location=0,menubar=0,fullscreen=yes');
    }
}

MulticartCancelOrderPopup.defaultProps = {
    cancelOrderWarning: "Chcesz anulować zamówienie, które opłacił Klient. Pamiętaj aby dokonać korekty tego zamówienia. Przejdź do Fiori klikając ten ",
    cancelOrderWarningLink: "link",
    cancelOrderQuestion: "Czy na pewno chcesz anulować zamówienie?",
    orderPayed: false,
    autoClose: true,
    onConfirm: function(event) {},
    onReject: function(event) {},
    onCorrectiveInvoiceButtonClick: function(event) {},
    noButtonText: "Nie",
    yesButtonText: "Tak",
    openPopup: false,
    showWarning: true
};